import React from "react";
import "./UserAvatar.css";

const UserAvatar = ({ user }) => {
  return (
    <>
      <img
        src={user.picture}
        alt={user.name}
        title={user.name}
        className={"avatar"}
      ></img>
      <p>{user.name}</p>
    </>
  );
};

export default UserAvatar;
