import React from 'react'
import '../signIn/SignIn.scss'
import makeToast from "../toaster";





class Register extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            email: '',
            password: '',
            name: '',
            surname: ''
        }
    }

    handleChange = (event, type) => {
        this.setState({
            [type]: event.target.value,
        })
    }

    handleSignUp = (event) => {
        event.preventDefault()
        let regexp = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        let { email, password, name, surname } = this.state

        if (regexp.test(email) ) {
            if(password.length > 4){
                let body =
                {
                    email,
                    password,
                    name,
                    surname
                }
                fetch('https://classchatmessanger.herokuapp.com/user/', {
                    method: 'POST',
                    headers: {
                        "Content-Type": "application/json"
                    },
                    body: JSON.stringify(body)
                })
                    .then((res) => res.json())
                    .then((response) => {
                        if (response.error) {
                            throw response.error
                        }
                        if (response.error) {
                            console.log(response.error.message, '----response.mes');
                        }
                        else {
                            makeToast("success", "Successfull registration");
                            this.props.history.push('/sign-in')
    
    
                        }
                       
    
                    })
                    .catch((error) => {
                        makeToast("error", error.message);
                        console.log(error);
                    })
    
            }
            else {
                makeToast("warning",'Password should be more than 5 characters');
    
            }
            
        } 
        else {
            makeToast("warning",'The email address field must contain a valid email address');

        }

    }


    render() {
        return (
            <div className='background'>

                <form className='form'
                    onSubmit={this.handleSignUp} >
                    <div>
                        <label className="label-email">
                        <span className="required">Name</span>
                            <input className='inp'
                                type="text"
                                name="Name"
                                placeholder="Name"
                                required
                                value={this.state.name}
                                onChange={(event) => this.handleChange(event, 'name')} />
                        </label>
                    </div>
                    <div>
                        <label className="label-email">
                        <span className="required">Surname</span>
                            <input className='inp'
                                type="text"
                                name="Surname"
                                placeholder="Surname"
                                required
                                value={this.state.surname}
                                onChange={(event) => this.handleChange(event, 'surname')} />
                        </label>
                    </div>
                    <div>
                        <label className="label-email">
                        <span className="required">Email</span>
                            <input 
                                type="email"
                                className="text inp"
                                name="email"
                                placeholder="Email"
                                required
                                value={this.state.email}
                                onChange={(event) => this.handleChange(event, 'email')} />
                        </label>
                    </div>
                    <input type="checkbox" name="show-password" className="inp show-password a11y-hidden" id="show-password"
                    />
                    <label className="label-show-password" htmlFor="show-password">
                        <span>Show Password</span>
                    </label>
                    <div>
                        <label className="label-password">
                        <span className="required">Password</span>
                            <input 
                                type="text"
                                className="text inp"
                                name="password"
                                placeholder="Password"
                                required
                                value={this.state.password}
                                onChange={(event) => this.handleChange(event, 'password')} />
                        </label>
                    </div>
                    <input className='inp' type="submit" value="Sign up" />
                    <figure aria-hidden="true">
                        <div className="person-body"></div>
                        <div className="neck skin"></div>
                        <div className="head skin">
                            <div className="eyes"></div>
                            <div className="mouth"></div>
                        </div>
                        <div className="hair"></div>
                        <div className="ears"></div>
                        <div className="shirt-1"></div>
                        <div className="shirt-2"></div>
                    </figure>
                </form>
            </div>

        )
    }


}

export default Register